.form-arrange {
    display: flex;
    border-radius: 10px;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    /* align-items: flex-end; */
    justify-content: space-around;
}

.detail-header {
    font-weight: bold;
    font-size: x-large;

}

.btn-submit {
    background-color: #4caf50;
    color: #fff;
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    border-radius: 30px;
    cursor: pointer;
    margin-top: 12px;


}

.role {
    margin-left: 29%;
}

.select-details {
    width: 206px;
    border-radius: 52px;

}